<template>
    <div class="qiong-txt-td">
        <v-treeview multiple-active transition dense off-icon slot="tree"
            class="mt-0 qiong-the-tree"

            :items="users" open-all

            v-if="users"
        >
            <template v-slot:label="{ item, open }">
                <div class="qiong-pl-20" @click="done(open)">
                    <named-viewing 
                        :named="item.display_name" :index="item.index" 
                        :need_icon="item.has_child"
                    ></named-viewing>
                </div>
            </template>
            <template v-slot:append="{ item, open }">
                <ust-tree-item :_item="item" :o="open"></ust-tree-item>
            </template>
        </v-treeview>
        
        <div v-if="users">
            <qiong-icon-outline 
                :is_fixed="true" :l_or_r="false" :is_small="false" 
                :extra_calss="'right_20 top_80 mr-0 qiong-btn-fixed-shadow'"
                v-if="this.$route.query.id"

                @done_Father="back"
                ></qiong-icon-outline>
        </div>
    </div>
</template>

<script>
import QiongIconOutline from '../../../../components/Qiong/Button/QiongIconOutline.vue'
import NamedViewing from '../../../../components/Viewing/NamedViewing.vue'
import UstTreeItem from '../Item/UstTreeItem.vue'
    export default {
        inject: [ 'reload' ],
        watch: {
            $route(to, from) {
                if (from.query) {
                    this.mine = null
                    this.childrens = null
                    this.reload()
                }
            }
        },
        
        components: { UstTreeItem, NamedViewing ,
            QiongIconOutline
        },

        name: '',
        data() {
            return {
                id: null,
                op: [ 0, 1, 2, 3 ],
                show: true,
                users: null
            }
        },
        mounted() {
            if (this.$route.query.id) {
                this.id = this.$route.query.id
            }

            if (this.id == null) {

                this.users = this.$store.state.users_sponser
            } else {
                this.me()
            }

            console.log('执行')
        },
        methods: {
            done(o) {
                o = true
            },

            me(sort = true) {
                const _this = this
                let res = null
                const many = this.$store.state.users_sponser
                const transfer = this.$store.state.transfer

                if (many) {

                    this.bonus._build_res(many, transfer, function (u, t) {

                        if (u.unid == _this.id) {

                            res = [ JSON.parse(JSON.stringify(u)) ]
                        }
                    })
                }

                if (res.length > 0) {
                    const i = res[0].index
                    this.bonus._build_res(res, transfer, function (u, t) {                        
                        u.index = u.index - i
                    })

                    this.users = res
                }   
            },

            back() {
                this.$router.back()

                // this.reload()
            }
        },
    }
</script>

<style lang="sass" scoped>
    
</style>


