<template>
    <div class="qiong-td qiong-td-h-full qiong-line-hight-15">

        <div class="qiong-wide-18 ">{{ _item.number_code || 'A' }}</div>
        <div class="qiong-wide-19">{{ _item.sponser_id }}</div>
        <div class="qiong-wide-12">{{  }}</div>
        <div class="qiong-wide-12">{{ _item.ipv }}</div>
        <div class="qiong-wide-12">{{  }}</div>
        <div class="qiong-wide-12">{{ _item.son_pv }}</div>
        <div class="qiong-wide-15">
            <qiong-icon-inset :color="'primary'" 
                v-if="_item.index != 0 && _item.has_child"
            :arg="_item" @done_Father="openCenter"></qiong-icon-inset>
        </div>

    </div>
</template>

<script>
import QiongIconInset from '../../../../components/Qiong/Button/QiongIconInset.vue'
    export default {
  components: { QiongIconInset },
        inject: [ 'reload' ],
        props: {
            _item: {
                type: Object
            }
        },
        name: '',
        data() {
            return {
                
            }
        },
        methods: {
            openCenter(item) {
                // /home/user/sponser


                this.$router.push({
                    path: '/home/user/sponser', query: { 'id': item.unid } })

                this.reload()
            }
        },
    }
</script>

<style lang="sass" scoped>
    
</style>


